<template>
  <div id="search-results">
    <b-table
      :data="isEmpty ? [] : tableData"
      :bordered="isBordered"
      :striped="isStriped"
      :narrowed="isNarrowed"
      :loading="isLoading"
      :mobile-cards="hasMobileCards"
      default-sort-direction="desc"
      default-sort="report.date">
      <template slot-scope="props">
        <b-table-column field="report.date" label='Date' centered sortable>
          {{ props.row.date }}
        </b-table-column>
        <b-table-column label='Report' centered>
          {{ props.row.report }}
        </b-table-column>
      </template>
      </b-table>
  </div>
</template>

<script>
export default {
  props: ['tableData'],
  data () {
    return {
      isEmpty: false,
      isBordered: false,
      isStriped: true,
      isNarrowed: false,
      isLoading: false,
      hasMobileCards: true
    }
  }
}
</script>




